import React, {
	forwardRef,
	KeyboardEvent,
	Ref,
	useEffect,
	useImperativeHandle,
	useRef,
	useState,
} from "react";

import { Icon, IconName } from "../Icon/Icon";
import { ComponentWrapper, InputWrapper } from "../Input/Input.styles";
import { Label } from "../Label";
import { colors, Margins } from "../styleguide";

import { DEFAULT_FORMAT_PATTERN } from "./DatePicker.consts";
import {
	getValidDate,
	handleInvalidDate,
	safeDateFormat,
	setNewEndDate,
	setNewStartDate,
} from "./DatePicker.utils";
import {
	EndDateInput,
	EndDateWrapper,
	StartDateInput,
	StartDateWrapper,
	StyledLine,
	StyledRightIcon,
	ValueWrapper,
} from "./DateRangePickerInput.styles";

export type DateRangePickerInputProps = Margins & {
	dataQA: string,
	id: string,
	required?: boolean,
	label?: string,
	dateFormat?: string,
	endDateInputRef?: Ref<HTMLInputElement> | null;
	onDateChange?: (date: Date | Date[]) => void;
	selectedDates?: Date[];
	placeholder?: string;
	minDate?: Date;
	maxDate?: Date;
	inputLeftIcon?: IconName;
	inputRightIcon?: IconName;
	className?: string;
};

const DateRangePickerInput = forwardRef<HTMLInputElement, DateRangePickerInputProps>(({
	dataQA,
	id,
	label,
	required,
	dateFormat = DEFAULT_FORMAT_PATTERN,
	endDateInputRef,
	onDateChange,
	selectedDates,
	placeholder,
	minDate,
	maxDate,
	inputLeftIcon,
	inputRightIcon,
	className,
	m,
	ml,
	mb,
	mr,
	mt,
	...propsInheritedFromRadixTrigger
}, startDateInputRef) => {
	const [startDateValue, setStartDateValue] = useState("");
	const [endDateValue, setEndDateValue] = useState("");

	const validStartDate = getValidDate(startDateValue, dateFormat, minDate, maxDate);
	const validEndDate = getValidDate(endDateValue, dateFormat, minDate, maxDate);

	const innerEndDateInputRef = useRef<HTMLInputElement>(document.createElement("input"));

	useImperativeHandle(endDateInputRef, () => innerEndDateInputRef.current);

	useEffect(() => {
		if (!selectedDates) {
			return;
		}

		const [startDate, endDate] = selectedDates;

		const newStartDateValue = startDate ? safeDateFormat(startDate, dateFormat) : "";
		setStartDateValue(newStartDateValue);

		const newEndDateValue = endDate ? safeDateFormat(endDate, dateFormat) : "";
		setEndDateValue(newEndDateValue);
	}, [selectedDates, dateFormat]);


	/**
	 * @param newStartDate
	 * @description newStartDate is the input value that has been validated and turned into a date
	 */
	const handleStartDate = (newStartDate?: Date) => {
		const newSelectedDates = selectedDates instanceof Array ? [...selectedDates] : [];
		// if there is no new start date but there is a selected start date, set the selected dates to an empty array
		// this is subject to change as we get more people using this
		if (!newStartDate && newSelectedDates[0] && onDateChange) {
			onDateChange([]);
			return;
		}

		if (newStartDate && onDateChange) {
			setNewStartDate(
				newStartDate,
				newSelectedDates,
				onDateChange,
			);
		}
	};

	/**
	 * @param newEndDate
	 * @description newEndDate is the input value that has been validated and turned into a date
	 */
	const handleEndDate = (newEndDate?: Date) => {
		const newSelectedDates = selectedDates instanceof Array ? [...selectedDates] : [];

		// no new end date and no selected end date, return
		if (!newEndDate && !newSelectedDates[1]) {
			return;
		}

		// no new end date submitted but there is an already selected date, remove it
		if (!newEndDate && newSelectedDates[1] && onDateChange) {
			newSelectedDates.pop();
			onDateChange(newSelectedDates);
			return;
		}

		if (newEndDate && onDateChange) {
			setNewEndDate(
				newEndDate,
				newSelectedDates,
				onDateChange,
			);
		}
	};

	const handleOnBlurStartDate = () => {
		if (!validStartDate) {
			if (!startDateValue) {
				handleStartDate();
				return;
			}
			return selectedDates && handleInvalidDate(setStartDateValue, dateFormat, selectedDates[0]);
		}

		handleStartDate(validStartDate);
	};

	const handleOnBlurEndDate = () => {
		if (!validEndDate) {
			if (!endDateValue) {
				handleEndDate();
				return;
			}
			return selectedDates && handleInvalidDate(setEndDateValue, dateFormat, selectedDates[1]);
		}

		handleEndDate(validEndDate);
	};

	const handleOnEnterStartDate = (event?: KeyboardEvent<HTMLInputElement>) => {
		if (event?.key !== "Enter") {
			return;
		}

		if (!validStartDate) {
			if (!startDateValue) {
				handleStartDate();
				return;
			}

			return selectedDates && handleInvalidDate(setStartDateValue, dateFormat, selectedDates[0]);
		}

		handleStartDate(validStartDate);

		if (innerEndDateInputRef.current
			&& selectedDates
			&& !selectedDates[1]
			&& validStartDate
		) {
			innerEndDateInputRef.current.focus();
		}
	};

	const handleOnEnterEndDate = (event?: KeyboardEvent<HTMLInputElement>) => {
		if (event?.key !== "Enter") {
			return;
		}

		if (!validEndDate) {
			if (!endDateValue) {
				handleEndDate();
				return;
			}
			return selectedDates && handleInvalidDate(setEndDateValue, dateFormat, selectedDates[1]);
		}

		handleEndDate(validEndDate);
	};

	const hasTwoDates = () => {
		if (!selectedDates?.length || selectedDates instanceof Date) {
			return false;
		}

		if (selectedDates[0] && (endDateValue || selectedDates[1])) {
			return true;
		}

		return false;
	};

	return (
		<ComponentWrapper
			className={className}
			m={m}
			ml={ml}
			mb={mb}
			mr={mr}
			mt={mt}
		>
			{label
				&& (
					<Label
						dataQA={`${dataQA}-label`}
						htmlFor={id}
						id={`${dataQA}-label`}
						isRequired={required}
					>
						{label}
					</Label>
				)
			}
			<InputWrapper>
				{inputLeftIcon && (
					<Icon
						data-qa={`${inputLeftIcon}-left-icon`}
						name={inputLeftIcon}
						color={`${colors.black}54`}
						size={20}
						strokeWidth={2}
					/>
				)}
				<StartDateWrapper>
					<ValueWrapper>
						<span>{startDateValue || placeholder || dateFormat}</span>
					</ValueWrapper>
					<StartDateInput
						{...propsInheritedFromRadixTrigger}
						autoComplete="off"
						type="text"
						placeholder={placeholder || dateFormat}
						id={id}
						onChange={(event) => event && setStartDateValue(event.target?.value)}
						ref={startDateInputRef}
						onKeyUp={handleOnEnterStartDate}
						onBlur={handleOnBlurStartDate}
						aria-label="enter a start date"
						value={startDateValue}
						data-qa={`${dataQA}-start-date-input`}
					/>
				</StartDateWrapper>
				<StyledLine hasTwoDates={hasTwoDates()}/>
				<EndDateWrapper>
					<ValueWrapper>
						<span>{endDateValue || placeholder || dateFormat}</span>
					</ValueWrapper>
					<EndDateInput
						{...propsInheritedFromRadixTrigger}
						autoComplete="off"
						type="text"
						placeholder={placeholder || dateFormat}
						id={`${id}-end-date`}
						onChange={(event) => event && setEndDateValue(event.target?.value)}
						aria-label="enter an end date"
						ref={innerEndDateInputRef}
						onBlur={handleOnBlurEndDate}
						onKeyUp={handleOnEnterEndDate}
						value={endDateValue}
						data-qa={`${dataQA}-end-date-input`}
					/>
				</EndDateWrapper>
				{inputRightIcon && (
					<StyledRightIcon
						data-qa={`${inputRightIcon}-right-icon`}
						name={inputRightIcon}
						color={`${colors.black}54`}
						size={20}
						strokeWidth={2}
					/>
				)}
			</InputWrapper>
		</ComponentWrapper>);
});

DateRangePickerInput.displayName = "DateRangePickerInput";

export default DateRangePickerInput;
